.drop-menu__list {
  position: absolute;
  overflow: hidden;
  width: 29.2rem;
  text-align: left;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-35%);
}

.drop-menu__list::before {
  content: '';
  position: absolute;
  width: 0.9rem;
  height: 0.9rem;
  background-color: currentColor;
  transform: rotate(45deg) translate(-50%, 0%);
}

.drop-menu__btn {
  position: relative;
  z-index: 21;
  display: inline;
}

.drop-menu__link {
  text-decoration: underline;
  color: #000;
}

.drop-menu__link:hover {
  color: #bd9457;
}

.drop-menu__list--first {
  left: 0;
  top: 150%;
  z-index: 21;
  padding: 0 1.7rem;
  transition: visibility 0.4s linear, opacity 0.22s linear, padding 0.32s linear;
}

.drop-menu__list--first::before {
  left: 50%;
  top: 0;
  opacity: 0;
}

.drop-menu.open .drop-menu__list--first {
  overflow: visible;
  padding: 2.4rem 1.7rem;
  opacity: 1;
  visibility: visible;
}

.drop-menu.open .drop-menu__list--first::before {
  opacity: 1;
  transition: opacity 0.2s linear;
}

.drop-menu.open .drop-menu__btn {
  color: #bd9457;
}

.drop-menu__list--first li {
  position: relative;
}

.drop-menu__list--second {
  left: 0;
  top: 0;
  z-index: -1;
  padding: 2.4rem 1.7rem;
  transform: translate3d(-4%, 0, 0);
  transition: visibility 0.1s linear, opacity 0.2s linear, transform 0.2s linear, z-index 0.2s linear;
}

.drop-menu__list--second::before {
  left: 0;
  top: 13%;
}

.drop-menu__item:hover .drop-menu__list--second {
  z-index: 21;
  display: block;
  overflow: visible;
  opacity: 1;
  visibility: visible;
  transform: translate3d(100%, 0, 0);
}

.drop-menu__item:hover .drop-menu__list--second::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 17%;
  height: 100%;
  background-color: transparent;
  transform: translateX(-100%);
}

