    color: #000;
  }

  .drop-menu .drop-menu__list--second {
    margin-bottom: 1.5rem;
  }

  .drop-menu__item:hover .drop-menu__list--second {
    transform: translate3d(0, 0, 0);
  }

  .drop-menu__item:hover .drop-menu__list--second::after {
    content: '';
  }

  .drop-menu .drop-menu__link:hover {
