
.social {
  &__link {
    padding: 0 4px;

    svg {
      width: 1.5rem;
      height: 1.5rem;
    }
  }

  @include mw3 {
    &__link {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      width: 4rem;
      height: 4rem;

      svg {
        vertical-align: middle;
        width: 2.5rem;
        height: 2.5rem;
      }
    }
  }
}