// click on button shadow wave

@keyframes anim-click-open {
  0% {
    opacity: 1;
    transform: scale3d(0.4, 0.4, 1);
  }

  80% {
    box-shadow: inset 0 0 0 2px $firstcolor;
    opacity: 0.1;
  }

  100% {
    box-shadow: inset 0 0 0 1px $seccolor;
    opacity: 1;
    transform: scale3d(1.2, 1.2, 1);
  }
}