@mixin cont {
  @media (max-width: (1920px)) {
    @content;
  }
}

@mixin hd {
  @media (max-width: (1480px)) {
    @content;
  }
}

@mixin mw1 {
  @media (max-width: (1025px)) {
    @content;
  }
}

@mixin mw2 {
  @media (max-width: (1020px)) {
    @content;
  }
}

@mixin mw3 {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mw4 {
  @media (max-width: (565px)) {
    @content;
  }
}

@mixin mw5 {
  @media (max-width: (440px)) {
    @content;
  }
}

//  hover on

@mixin hover {
  @media (min-width: (768px)) {
    @content;
  }
}