.contacts {
  &__inner {
    text-align: right;
  }

  &__tel-box {
    display: block;
  }

  &__tel {
    display: block;
    font-weight: 600;
    font-size: $fslarge;
    line-height: 1.35;
  }

  &__box {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  &__callback {
    display: none;
  }

  &__text {
    display: block;
    margin-right: 0.7rem;
    padding-bottom: 0.8rem;
    font-size: $fsmicro;
    pointer-events: none;
  }

  // mobile button

  &__mobile {
    position: relative;
    display: none;
    overflow: visible;
    width: 4.4rem;
    height: 4.4rem;

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      margin: -2rem 0 0 -2rem;
      border-radius: 50%;
      width: 4rem;
      height: 4rem;
      opacity: 1;
    }

    svg {
      display: block;
      margin: auto;
      width: 2.5rem;
      height: 2.5rem;
    }
  }

  @include mw3 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &__tel {
      margin-right: 10px;
    }

    &__text {
      display: none;
    }
  }

  @include mw4 {
    justify-content: flex-end;
    margin: 0 1.2rem 0 auto;
    min-width: 0;

    &__inner {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 56;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 8rem 3rem 7rem;
      width: 100%;
      min-height: 100vh;
      text-align: center;
      background-color: $thirdcol;
      opacity: 0;
      visibility: hidden;
      transform: translate3d(0, -125%, 0);
      transition: transform 0.4s linear, opacity 0.4s linear, visibility 0.4s linear;
    }

    &__mobile {
      display: flex;
    }

    &__tel {
      margin-right: 0;
    }

    &__tel-box {
      margin-bottom: 1rem;
    }

    &__social {
      margin-bottom: 2.5rem;
    }

    &__callback {
      display: block;
    }
  }
}
