.nav {
  &__list {
    justify-content: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 38px;
    }
  }

  &__link {
    display: inline-block;
    padding: 0.5rem;
    font-size: $fssmall;
    line-height: 1.2;
    transition: color 0.2s linear;

    @include hover {
      &:hover {
        color: $thirdcol;
      }
    }
  }

  @include mw1 {
    &__list {
      display: block;
    }

    &__item {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 1rem;
      }
    }

    &__link {
      padding: 1rem;
      font-weight: 600;
      font-size: $fsExtrnrml;

      &:hover {
        color: $firstcolor;
      }
    }
  }
}

//  drop-menu

.drop-menu {
  position: relative;

  &__list {
    position: absolute;
    overflow: hidden;
    width: 29.2rem;
    text-align: left;
    background-color: $seccolor;
    opacity: 0;
    visibility: hidden;
    transform: translateX(-35%);

    // decorative

    &::before {
      content: '';
      position: absolute;
      width: 0.9rem;
      height: 0.9rem;
      background-color: currentColor;
      transform: rotate(45deg) translate(-50%, 0%);
    }
  }

  &__btn {
    position: relative;
    z-index: 21;
    display: inline;
  }

  &__link {
    text-decoration: underline;
    color: $firstcolor;

    &:hover {
      color: $thirdcol;
    }
  }

  @include mw1 {
    position: static;

    &__list {
      position: static;
      display: none;
      padding: 0;
      width: 100%;
      text-align: center;
      background-color: transparent;
      transform: translate(0);
      transition: none;

      &::before {
        content: none;
      }
    }

    &__link {
      padding: 0.8rem 0;
      font-size: $fsnrml;
      text-decoration: none;

      &:hover {
        color: $firstcolor;
      }
    }
  }
}

// by click menu lvl 1

.drop-menu {
  &__list--first {
    left: 0;
    top: 150%;
    z-index: 21;
    padding: 0 1.7rem;
    transition: visibility 0.4s linear, opacity 0.22s linear, padding 0.32s linear;

    &::before {
      left: 50%;
      top: 0;
      opacity: 0;
    }
  }

  &.open {
    .drop-menu__list--first {
      overflow: visible;
      padding: 2.4rem 1.7rem;
      opacity: 1;
      visibility: visible;

      &::before {
        opacity: 1;
        transition: opacity 0.2s linear;
      }
    }

    .drop-menu__btn {
      color: $thirdcol;
    }
  }

  @include mw1 {
    &.open {
      .drop-menu__list--first {
        display: block;
        padding: 0;
      }

      .drop-menu__btn {
        color: $firstcolor;
      }
    }
  }
}

// by hover menu lvl 2

.drop-menu {
  &__list--first {
    li {
      position: relative;
    }
  }

  &__list--second {
    left: 0;
    top: 0;
    z-index: -1;
    padding: 2.4rem 1.7rem;
    transform: translate3d(-4%, 0, 0);
    transition: visibility 0.1s linear, opacity 0.2s linear, transform 0.2s linear, z-index 0.2s linear;

    &::before {
      left: 0;
      top: 13%;
    }
  }

  &__item {
    &:hover {
      .drop-menu__list--second {
        z-index: 21;
        display: block;
        overflow: visible;
        opacity: 1;
        visibility: visible;
        transform: translate3d(100%, 0, 0);

        &::after {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 17%;
          height: 100%;
          background-color: transparent;
          transform: translateX(-100%);
        }
      }
    }
  }

  @include mw1 {
    .drop-menu__list--second {
      margin-bottom: 1.5rem;
    }

    &__item {
      &:hover {
        .drop-menu__list--second {
          transform: translate3d(0, 0, 0);

          &::after {
            content: '';
          }
        }
      }
    }

    .drop-menu__link:hover {
      text-decoration: underline;
    }
  }
}