.callback {
  padding: 5rem 4rem;
  width: 100%;
  max-width: 39.2rem;
  background-color: $seccolor;

  &__title {
    margin-bottom: 1.5rem;
    font-weight: 500;
    font-size: $fslarge;
  }

  &__descr {
    margin-bottom: 2.5rem;
  }

  &__field {
    display: block;
    border: 1px solid $sixthcolor;
    border-radius: 5rem;
    padding: 1.5rem 2rem;
    width: 100%;
    color: $sevencolor;

    & + & {
      margin-top: 1.5rem;
      margin-bottom: 2rem;
    }
  }

  &__pers-information {
    margin-bottom: 2.5rem;
  }

  &__submit {
    padding: 1.65rem;
    width: 100%;
    color: $seccolor;
  }

  @include mw4 {
    padding: 3.5rem 1.5rem;
  }
}

.checkbox {
  position: relative;
  display: block;
  padding-left: 3rem;
  line-height: 1.6;

  &__input {
    position: absolute;
    left: 0;
    top: 0;
    width: 1.8rem;
    height: 1.8rem;
    transform: translateY(15%);
  }

  &__text {
  }

  &__link {
    display: inline;
    text-decoration: underline;
  }
}
