.burger {
  position: relative;
  z-index: 55;
  display: none;
  flex-shrink: 0;
  flex-direction: column;
  padding: 1rem 0;
  width: 4.4rem;
  height: 4.4rem;
  cursor: pointer;

  span,
  &::before,
  &::after {
    margin: auto;
    border-radius: 0.5rem;
    width: 80%;
    height: 0.4rem;
    background-color: $seccolor;
    transition: transform 0.2s linear, opacity 0.1s linear;
  }

  &::before,
  &::after {
    content: '';
  }

  &.active {
    &::before,
    &::after {
      // transform: translate3d(-50%, 0 0);
    }

    &::before {
      transform: rotate(45deg) translate3d(15%, 150%, 0);
    }

    &::after {
      transform: rotate(-45deg) translate3d(15%, -150%, 0);
    }

    span {
      opacity: 0;
      transform: translate3d(200%, 0, 0);
    }
  }
}
