.hero {
  position: relative;
  min-height: 100vh;
  color: $seccolor;

  &__bg {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
      background-color: rgba(#000, 0.49);
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__container {
    z-index: 2;
    padding-top: 26.4rem;
    padding-bottom: 4.5rem;
  }

  &__suptitle {
    max-width: 250px;
    font-size: $fsSeminrml;
    line-height: 1.53;
  }

  &__title {
    display: block;
    margin-bottom: calc(1.7rem + 23 * ((100vw - 320px) / (1920 - 320)));
    max-width: 79rem;
  }

  &__list {
    margin-bottom: 8.5rem;
  }

  &__link {
    margin-bottom: 13rem;
    box-sizing: border-box;
  }

  // aside text <<

  &-aside {
    position: absolute;
    top: 35%;
    z-index: 10;
    padding: 0 6.5rem 0 0;
    width: 100%;
    box-sizing: border-box;

    &__container {
      justify-content: flex-end;
    }

    &__text {
      letter-spacing: 0.05rem;
      color: $firstcolor;
      transform: rotateZ(90deg);

      svg {
        display: inline-block;
        vertical-align: middle;
        margin-left: 1rem;
        width: 1.5rem;
        height: 1rem;
        fill: currentColor;
      }
    }
  }

  @include cont {
    &__container {
      padding-top: calc(17rem + 103 * ((100vw - 1480px) / (1920 - 1480)));
    }

    &__title {
      max-width: 65rem;
    }

    &__list {
      margin-bottom: calc(3rem + 55 * ((100vw - 320px) / (1920 - 320)));
    }

    &__link {
      margin-bottom: calc(4.5rem + 85 * ((100vw - 1480px) / (1920 - 1480)));
    }
  }

  @include hd {
    &__container {
      padding-top: calc(9.5rem + 65 * ((100vw - 320px) / (1920 - 320)));
    }

    &__link {
      margin-bottom: calc(2rem + 25 * ((100vw - 320px) / (1920 - 320)));
    }

    &-aside {
      padding: 0;
    }
  }

  @include mw1 {
    &-aside__text {
      display: none;
    }
  }

  @include mw4 {
    &__link {
      width: 100%;
    }
  }
}

.benefits-mini {
  flex-wrap: wrap;
  max-width: 82rem;
  pointer-events: none;

  &__item {
    align-items: center;

    &:not(:last-child) {
      margin-right: 2.8rem;
    }
  }

  &__icon {
    margin-right: 1rem;
    width: 6rem;
    height: 5.9rem;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    display: inline-block;
    max-width: 180px;
    line-height: 1.77;
  }

  @include mw4 {
    &__item {
      &:not(:last-child) {
        margin-right: 0;
      }
    }

    &__text {
      max-width: none;

      br {
        display: none;
      }
    }
  }
}

.hero-navigation {
  position: absolute;
  bottom: 17%;
  z-index: 10;
  width: 100%;

  &__container {
    justify-content: flex-end;
  }

  @include hd {
    bottom: 25%;
  }

  @include mw3 {
    display: none;
  }
}
